.wrapper {
  width: 400px;
  color: #000;
  text-align: center;
  background-color: #adff2f;
  border-radius: 10px;
  padding: 20px;
}

h1 {
  font-size: 32px;
  font-weight: 600;
}

.dice {
  margin: -20px 0;
  font-size: 100px;
}

.dice-rolling {
  animation: 1s forwards rolling;
}

@keyframes rolling {
  0% {
    transform: rotateZ(0)rotateY(0);
  }

  100% {
    transform: rotateZ(720deg)rotateY(720deg);
  }
}

.rollingButton {
  cursor: pointer;
  background-color: #000;
  border: none;
  border-radius: 10px;
  margin-bottom: 20px;
  padding: 8px 20px;
  font-size: 18px;
  font-weight: 500;
}

.rollingButton:active {
  transform: scale(.95);
}

li {
  box-sizing: 0 3px #000000b3, -3px -3px 10px #0000004d, 3px 0 10px #0000004d;
  cursor: pointer;
  background-color: #fff;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  margin: 15px 0;
  padding: 0 10px;
  display: flex;
}

span {
  font-size: 30px;
}

